.App {
  text-align: left;
}

.form {
  margin-top: 1%;
  margin-left: 12.5%;
  width: 75%;
  font-family: 'Montserrat', sans-serif;
}

.form input{
  font-family: 'Montserrat', sans-serif;
  padding: 8px;
  outline: none;
  border-radius: 8px;
  width: 75%;
  background-color: #30444d;
  color: white;
  border: 1px solid #1F2833;
  transition: 0.5s ease;
}
.form input:hover{
  border: 1px solid rgb(0, 71, 147);
}
.form input:focus{
  border: 1px solid rgb(0, 168, 224);
}

.form button{
  width: 15%;
  font-family: 'Montserrat', sans-serif;
  padding: 8px;
  color: white;
  font: x-large;
  background-color: rgb(0, 101, 210);
  border: none;
  border-radius: 8px;
  transition: .5s ease;
}
.form button:hover{
  cursor: pointer;
  background-color: rgb(0, 72, 148);
  color: rgb(207, 207, 207);
}
